













import {Component, Vue} from 'vue-property-decorator';
import ProcessExchangeScreen from '@/modules/exchange/views/ProcessExchangeScreen.vue';
import ProcessTransactionStep4
  from '@/modules/exchange/components/process-transaction-steps/ProcessTransactionStep4.vue';
import ExchangeTransactionRepository from '@/modules/exchange/repository/exchange-transaction-repository';
import ExchangeTransaction from '@/modules/exchange/models/exchange-transaction';

@Component({
  components: {
    ProcessExchangeScreen,
    ProcessTransactionStep4,
  },
})
export default class TransactionDetail extends Vue {
  loading: boolean = false;
  transaction: ExchangeTransaction | null = null;

  mounted() {
    this.fetchData();
  }

  async fetchData() {
    if (this.transactionId) {
      const transaction = await ExchangeTransactionRepository.getTransactionById(this.transactionId);
      if (transaction) {
        this.transaction = transaction;
      }
    } else {
      setTimeout(this.fetchData, 500);
    }
  }

  get transactionId() {
    return (this.$route.params as any).id;
  }

}
