



















import {Component, Vue, Watch} from 'vue-property-decorator';
import Wallet from '@/modules/wallet/models/wallet';
import WalletRepository from '@/modules/wallet/repository/wallet-repository';

@Component({})
export default class WalletScreen extends Vue {
  loading: boolean = true;
  tabs: any = [
    {
      value: 'assets',
      to: 'fiat-currencies',
    },
    {
      value: 'transactions',
      to: 'pending-transactions',
    },
    {
      value: 'deposit',
      to: 'deposit-paypal',
    },
    {
      value: 'withdraw',
      to: 'withdraw-paypal',
    },
  ];
  tabSelect = 0;
  wallet: Wallet | null = new Wallet();

  mounted() {
    this.fetchWallet();
  }

  @Watch('tabSelect')
  onSelectTab(index: string) {
    this.$router.push({name: this.tabs[index].to});
  }

  @Watch('$route') fetchData() {
    this.fetchWallet();
  }

  async fetchWallet() {
    this.loading = true;
    try {
      this.wallet = await WalletRepository.getMyWallet();
    } catch (e) {
    } finally {
      this.loading = false;
    }
  }
}
