



















import {Component, Vue, Watch} from 'vue-property-decorator';
import ProjectFilter, {EProjectType, IProjectFilter} from '@/modules/project/components/ProjectFilter.vue';

@Component({
  components: {
    ProjectFilter,
  },
})
export default class ExchangeScreen extends Vue {
  filter: IProjectFilter = {
    type: EProjectType.FUNDRAISING,
    category: undefined,
    country: undefined,
    searchText: '',
  };
  loading = true;
  tabs: any = [
    {
      value: 'list of buyer',
      to: 'buyer-list',
    },
    {
      value: 'list of seller',
      to: 'seller-list',
    },
  ];
  tabSelect = 0;

  @Watch('tabSelect')
  onSelectTab(index: string) {
    this.$router.push({name: this.tabs[index].to});
  }

  submit(id: string) {
    this.$router.push({name: 'process-exchange', params: {transactionId: id}});
  }

  gotoExchangeScreen() {
    this.$router.push({name: 'exchange-action'});
  }

}
