




















































































import {Component, Vue, Watch} from 'vue-property-decorator';
import ProjectRepository from '@/modules/project/repository/project-repository';
import Project from '@/modules/project/models/project';
import UserRepository from '@/modules/users/repository/user-repository';
import ProjectMembers from '@/modules/project/components/ProjectMembers.vue';
import ProjectAddUser from '@/modules/project/components/ProjectAddUser.vue';
import ExchangeTransactionRepository from '@/modules/exchange/repository/exchange-transaction-repository';
import ExchangeTransaction from '@/modules/exchange/models/exchange-transaction';
import ExchangeLogic from '@/modules/exchange/exchange-logic';
import * as firebase from 'firebase';

@Component({
  components: {
    ProjectMembers,
    ProjectAddUser,
  },
})
export default class PendingTransactionsTab extends Vue {
  project: Project | null = null;
  loading: boolean = false;
  transactions: ExchangeTransaction[] = [];
  displayTransactions: any = [];
  acceptLoading: boolean = false;
  rejectLoading: boolean = false;
  returnLoading: boolean = false;

  get projectId() {
    return (this.$route.params as any).projectId;
  }

  get headers() {
    const headers = [
      {text: 'Time', value: 'createdTime', align: 'center'},
      {text: 'Seller', value: 'email', align: 'center'},
      {text: 'Buyer', value: 'position', align: 'center'},
      {text: 'Asset', value: 'position', align: 'center'},
      {text: 'Amount', value: 'position', align: 'center'},
      {text: 'Price', value: 'position', align: 'center'},
      {text: 'Document', value: 'position', align: 'center'},
      {text: 'Action', value: 'position', align: 'right'},
    ];

    return headers;
  }

  @Watch('projectId', {immediate: true})
  async onProjectIdChange() {
    this.loading = true;
    this.project = await ProjectRepository.getProjectById(this.projectId);

    if (this.project) {
      this.transactions = await ExchangeTransactionRepository.getPendingTransactions();
      const promises = this.transactions.map(async (transaction) => {
        (transaction as any).buyer = await UserRepository.getProfileByUserId(transaction.toUserId!);
        (transaction as any).seller = await UserRepository.getProfileByUserId(transaction.fromUserId!);
        (transaction as any).document = await firebase.storage().ref('document').child(transaction.id).getDownloadURL();

        return transaction;
      });
      Promise.all(promises).then((result) => {
        this.displayTransactions = result;
        this.loading = false;
      });

    }
  }

  async reloadModel() {
    await this.onProjectIdChange();
  }

  async acceptTransaction(item: ExchangeTransaction) {
    this.acceptLoading = true;
    if (item.type === 'BUY') {
      const result = await ExchangeLogic.processBuyTransaction(item, this.projectId);
    } else {
      const result = await ExchangeLogic.processSellTransaction(item, this.projectId);
    }
    this.acceptLoading = false;
    this.reloadModel();
  }

  async rejectTransaction(item: ExchangeTransaction) {
    this.rejectLoading = true;
    const result = await ExchangeLogic.processRejectTransaction(item, this.projectId);
    this.rejectLoading = false;
    this.reloadModel();
  }

  async returnTransaction(item: ExchangeTransaction) {
    this.returnLoading = true;
    const result = await ExchangeLogic.processReturnTransaction(item, this.projectId);
    this.returnLoading = false;
    this.reloadModel();
  }
}
