


























































import {Component, Prop, Vue} from 'vue-property-decorator';
import * as firebase from 'firebase';

@Component({})
export default class ADocumentUpload extends Vue {
  @Prop({type: String, default: '', required: false}) id: string;
  @Prop({default: false, type: Boolean}) underline?: boolean;
  @Prop({default: 'Click here to upload your image', type: String}) label: string;

  fileUrl: string = '';
  loading: boolean = false;

  openUploadFile() {
    (this.$refs.filePicker as any).click();
  }

  onFileSelected() {
    this.fileUrl = '';
    const file = (this.$refs.filePicker as any).files[0];

    const ref = firebase.storage().ref('document');
    const metadata = {
      contentType: file.type,
    };
    const key = this.id ? this.id : (+new Date()) + '-' + file.name;
    const task = ref.child(key).put(file, metadata);
    this.loading = true;
    task
      .then((snapshot: any) => snapshot.ref.getDownloadURL())
      .then((url) => {
        this.fileUrl = url;
        this.loading = false;
        this.$emit('uploaded', url);
      });
  }

}
