





















import {Component, Prop, Vue, Watch} from 'vue-property-decorator';
import Wallet from '../../models/wallet';

@Component({})
export default class DepositScreen extends Vue {
  @Prop() wallet!: Wallet;
  loading: boolean = true;
  tabs: any = [
    {
      value: 'paypal',
      to: 'deposit-paypal',
    },
    {
      value: 'apollo wallet',
      to: 'deposit-apollo-wallet',
    },
  ];
  tabSelect = 0;

  @Watch('tabSelect')
  onSelectTab(index: string) {
    this.$router.push({name: this.tabs[index].to});
  }
}
