















import {Component, Prop, Vue, Watch} from 'vue-property-decorator';
import ProjectRepository from '../modules/project/repository/project-repository';
import Project from '@/modules/project/models/project';
import {ExchangeType} from '@/modules/exchange/models/exchange-transaction';

@Component({})
export default class AProjectSelect extends Vue {
  projects: Project[] = [];
  @Prop({default: 'BUY'}) typeTransaction: ExchangeType;

  mounted() {
    this.fetchData();
  }

  async fetchData() {
    try {
      if (this.typeTransaction === 'BUY') {
        this.projects = await ProjectRepository.getProjects();
      } else {
        this.projects = await ProjectRepository.getMyProjects();
      }
    } catch (e) {
    }
  }

  @Watch('typeTransaction') reloadData() {
    this.fetchData();
  }
}
