




















































import {Component, Prop, Watch, Vue} from 'vue-property-decorator';
import Project from '@/modules/project/models/project';
import Wallet from '@/modules/wallet/models/wallet';
import {currency_format} from '@/utils/format';

@Component({
} as any)
export default class InvestStep2 extends Vue {
  @Prop() value!: any;
  @Prop() wallet!: Wallet;
  @Prop() project!: Project;
  @Prop({type: Boolean, default: false}) loading: boolean;

  minimumInvestment: number = 0;

  get total() {
    if (!this.project.currentFundRaising) {
      return 0;
    }
    return this.project.currentFundRaising.price * this.value.tokenAmount;
  }

  get currencyFormat() {
    return currency_format;
  }

  goToNextAction() {
    this.$emit('next');
  }

  goBackAction() {
    this.$emit('back');
  }
}
