











































































import {Component, Prop, Vue, Watch} from 'vue-property-decorator';
import ANavigation from './ANavigation.vue';
import ALogo from '@/components/ALogo.vue';
import ANotificationListButton from '@/components/ANotificationListButton.vue';
import * as firebase from 'firebase';

@Component({
  components: {
    ANavigation,
    ALogo,
    ANotificationListButton,
  },
  notifications: {
    showLogoutError: {
      title: 'Error',
      message: 'Logout Failed',
      type: 'error',
    },
    showLogoutSuccess: {
      title: 'Success',
      message: 'Logout Successfully',
      type: 'success',
    },
  },
} as any)
export default class AHeader extends Vue {
  @Prop({type: Boolean, default: false}) hideNavigation!: boolean;

  avatar: string = '';

  get user() {
    return this.$store.state.user.user;
  }

  signOutAction() {
    this.$store.dispatch('logout');
    (this as any).showLogoutSuccess();
  }

  goToProfileScreen() {
    this.$router.push('/profile');
  }

  get userId() {
    return firebase.auth().currentUser!.uid;
  }

  async mounted() {
    try {
      this.avatar = await firebase.storage().ref('avatar').child(this.userId).getDownloadURL();
    } catch (e) {
      this.avatar = require('@/assets/avatar-placeholder.png');
    }
  }
}
